.weather{
    width:300px;
    border-radius: 6px;
    background: #333;
    color: #fff;
    box-shadow: 10px -2px 20px rgba(0 0 0 /30%);
    margin: 20px auto;
    padding: 0 20px 20px 20px;
}
.top,
.bottom{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.city{
    font-weight: 600;
    font-size:20px;
    line-height: 1;
    margin-bottom: 0;
}
.desc{
    font-weight:300;
    font-size: 14px;
    margin: 0;
    line-height: 1;
    letter-spacing: 1px;
}
.icon{
    width: 100px;
}

.temp{
    font-size: 35px;
    font-weight: 800;
    margin: 10px 0;
}
.details{
    width: 100%;
    padding-left: 20px ;
}
.row{
    display: flex;
    justify-content: space-between;
}
.label{
    text-align: left;
    font-weight: 400;
    font-size: 12px;
    margin-bottom: 5px;
}
.value{
    font-weight: 600;
    font-size: 14px;
    margin-bottom: 5px;
}